<template>
  <v-container fluid>
    <v-layout
      wrap
      class="mb-5"
    >
    <v-flex lg4>
      <span>
      <v-text-field
       v-if="apiSelected=='techTransitApi'"
        v-model="termToSearch"
        hide-details
        prepend-icon="mdi-magnify"
        placeholder="Search all courses in TechTransit API (Press Enter) "
        single-line
        v-on:keyup.enter="searchByNameTechTransitApi()"
      ></v-text-field>
      <v-text-field
       v-if="apiSelected=='marcosApi'"
        v-model="termToSearch"
        hide-details
        prepend-icon="mdi-magnify"
        placeholder="Search all courses in Marco's API (Press Enter) "
        single-line
        v-on:keyup.enter="searchByNameTechTransitApi()"
      ></v-text-field>
      </span>
    </v-flex>
    
    </v-layout>
    <v-layout
      wrap
      row
      class="ml-5 mr-5"
    >
     <v-flex v-if="items.length==0"  lg12 text-lg-center>
          <p>We haven't found any course with this name, click <strong v-on:click="restartSearch()" style="color:blue; cursor:pointer;">HERE</strong> to search again.</p>
      </v-flex>
     <template  v-for="(item,indexItem) in items">
      <v-flex v-if="items.length>0" :key="'item-'+indexItem" lg4>
        <v-card flat tile class="ml-2 mr-2 mt-7" height="300" >
            <v-toolbar flat tile color="grey lighten-2">
              <v-spacer></v-spacer>
              <v-chip class="ma-2" light color="white">{{item.maximumCredits}} Credits</v-chip>
            </v-toolbar>
            <v-card-title style="font-size:16px;"  class="mt-6 pt-6">
              {{item.name}}
            </v-card-title>
            <v-card-subtitle>
              {{item.provider.name}}
            </v-card-subtitle>
            <v-card-actions>
              <v-card-text>
                <span v-if="item.price>0" class="price">{{item.price}} USD</span>
                <span v-if="item.price==0" class="price">FREE</span>
              </v-card-text>
              <v-spacer></v-spacer>
              <v-rating
                v-model="item.rating"
                color="yellow darken-3"
                background-color="grey darken-1"
                empty-icon="$ratingFull"
                readonly
                half-increments
                
              ></v-rating>
            </v-card-actions>
            <v-btn 
              
              absolute
              top 
              left
              height="82"
              width="82"
              >
                <v-avatar tile size="82">
                  <img :src="logo">
                </v-avatar>
            </v-btn>  

        </v-card>
      </v-flex>

      
      
     </template>
    
        <v-flex lg4 v-if="isLoading==true" >
          <v-skeleton-loader
              class="mx-auto"
              max-width="400"
              type="card"
            ></v-skeleton-loader>
        </v-flex>
        <v-flex lg4 v-if="isLoading==true" >
          <v-skeleton-loader
              class="mx-auto"
              max-width="400"
              type="card"
            ></v-skeleton-loader>
        </v-flex>
        <v-flex lg4 v-if="isLoading==true" >
          <v-skeleton-loader
              class="mx-auto"
              max-width="400"
              type="card"
            ></v-skeleton-loader>
        </v-flex>
    </v-layout>
    <div @scroll="onScroll()"></div>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: 'Courses',
  props: ['apiSelected'],
  data: () => ({
   rating:4.5,
   logo:require('@/assets/logo.png'),
   items:[],
   termToSearch:"",
   isLoading:"",
   scrolledToBottom:false,
   nextUrl:null,
   isLoading:false,
  }),
  created() {
    this.searchByNameTechTransitApi();
  },
  mounted() {
  this.scroll();
  },
  methods:{
    scroll(){
      window.onscroll = () => {
      let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;

      if (bottomOfWindow) {
        this.searchNextPage();
      }
    };
    },
    searchByNameMarcosApi()
    {
      let that =this;
      // let myUrl = "https://test.mytablemesa.com/api/courses?orderBy=popularity+desc&expand=provider&limit=24&profession=&subjectAreaCode=&state=&provider=&name=";
      let myUrl = this.baseURL+`/api/courses/?name=${this.termToSearch}`
     
      axios.get(myUrl)
        .then(function (response) {
          // handle success
          console.log(response.data.items);
          that.$set(that,"items",response.data.items);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
    },
    searchNextPage()
    {
      // alert("Searching next results");
       let that =this;
      // let myUrl = "https://test.mytablemesa.com/api/courses?orderBy=popularity+desc&expand=provider&limit=24&profession=&subjectAreaCode=&state=&provider=&name=";
      let myUrl = this.baseURL+`/api/tech-courses-next`
      that.$set(that,"isLoading",true);
      axios({
          method:'post',
          url:myUrl,
          data:{
            nextUrl:that.nextUrl,
          },
         
      })
      .then(response => {
          console.log(response.data.items);
          let theItems = response.data.items;
          that.items.push(...theItems);
          that.$set(that,"isLoading",false);
          that.$set(that,"nextUrl",response.data.next);
      })
      .catch(function (error) 
      {
          console.log("error..."+error);
          
      });
    },
    searchByNameTechTransitApi()
    {
      let that =this;
      // let myUrl = "https://test.mytablemesa.com/api/courses?orderBy=popularity+desc&expand=provider&limit=24&profession=&subjectAreaCode=&state=&provider=&name=";
      let myUrl = this.baseURL+`/api/tech-courses/?name=${this.termToSearch}`
     
      axios.get(myUrl)
        .then(function (response) {
          // handle success
          console.log(response.data.items);
          that.$set(that,"items",response.data.items);
          that.$set(that,"nextUrl",response.data.next);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
    },
    restartSearch()
    {
        this.$set(this,'termToSearch',"");
        this.searchByNameTechTransitApi();

    }
  }
}
</script>
<style scoped>
.price
{
  color: #1976D2;
  font-weight: bold;
  font-size: 16px;
}
.fab-container {
    z-index:4;
    position: fixed;
    bottom: 30px;
    right: 0;
    font-family:  'Josefin Sans', sans-serif;
  }
  .btnAvatar
  {
     height: 82px;
     width: 82px;
  }
  .avatar{
    top:50px;
    left:50px;
  }
</style>
